import Layout from '../components/Layout'
import Link from 'next/link'

const PostLink = () => (
    <li>
        <Link href="/post?title=React">
            <a>React Post</a>
        </Link>
    </li>
)

export default () => (
    <Layout title="My Blog">
        <ul>
            <PostLink title="react" />
        </ul>
    </Layout>
)